@font-face {
    font-family: 'petita';
    src: url('resources/fonts/petita_bold.ttf');
}

#bml-canvas {
  width: 100vw;
  height: 100vh;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
}

.Toolbar {
  width: 100%;
  height: 45px;
  background: rgba( 255, 255, 255, 0.75 );
  border-top: 3px solid rgba( 205, 205, 205, 0.45 );
  position: absolute;
  left: 0px;
  bottom: 0px;
}
.Toolbar > * {
  display: inline-block;
  vertical-align: middle;
  -webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 10px, rgba(0, 0, 0, 0.16) 0px 2px 5px;
          box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 10px, rgba(0, 0, 0, 0.16) 0px 2px 5px;
}

.Title {
  margin: 4px 18px 4px 8px;
  padding-left: 5px;
  text-align: center;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.Title a {
  text-decoration: none;
  font-family: 'petita';
  font-size: 29px;
  color: #111;
  opacity: 0.75;
}

.RangePicker {
  margin: 3px 8px 3px 8px;
  opacity: 0.9;
}
.range-label {
  font-size: 14px;
}
.range-label span {
  float: right;
}

.Color {
  margin: 3px 8px 7px 8px;
  opacity: 0.9;
}
.color-label {
  font-size: 14px;
}
.color-patch {
  width: 80px;
  height: 18px;
  cursor: pointer;
  border: 1px solid #666;
}
.color-picker {
  position: absolute;
  -webkit-transform: translate( calc(-50% + 40px), -100% );
  -ms-transform: translate( calc(-50% + 40px), -100% );
  transform: translate( calc(-50% + 40px), -100% );
}
.color-picker > div:first-child > div:first-child {
  border-radius: 0px !important;
}
.picker-arrow-down {
  width: 0; 
  height: 0; 
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  
  border-top: 6px solid #555;
  margin: auto;
  opacity: 1.9;
}

.Reinitiate {
  margin: 10px 18px 8px 8px;
  opacity: 0.9;
}
.Reinitiate div {
  font-size: 20px;
  color: #111;
  border: 1px solid #666;
  cursor: pointer;
  padding: 1px 6px 1px 6px;
  -webkit-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}
.Reinitiate div:hover {
  background-color: #555;
  color: #fff; 
}


